@import 'normalize.less';
@import 'montserrat.less';
@import 'slick.less';

* {
  box-sizing: border-box;
}
p {
  margin-top: 0;
  margin-bottom: 0;
}

html {
  // overflow: hidden;
  // height: 100vh;
}
body {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  overflow: hidden;
  font-family: 'Montserrat';
  background: #1e1e1e;
}
.app-viewport {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  overflow: hidden;
}
.app-screen {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  // background: #fff;
}
.app-screen-inn {
  margin: auto;
  width: 100%;
  position: relative;
  z-index: 1;
}
.app-screen-content {
  padding-left: 140px;
  padding-right: 180px;
  padding-top: 150px;
  padding-bottom: 80px;
  @media (max-width: 1400px) {
    padding-top: 140px;
    padding-left: 130px;
    padding-right: 160px;
    padding-bottom: 70px;
  }
  @media (max-width: 1000px) {
    padding-top: 120px;
    padding-bottom: 60px;
    padding-left: 120px;
    padding-right: 140px;
  }
  @media (max-width: 800px) {
    padding-top: 110px;
    padding-bottom: 70px;
    padding-left: 30px;
    padding-right: 30px;
  }
  @media (max-width: 480px) {
    padding-left: 25px;
    padding-right: 25px;
    padding-top: 80px;
  }
  &.__with-footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-bottom: 0;
    min-height: 100vh;
  }
}
.app-screen-content-inn {
  width: 100%;
  flex: 1;
  padding-bottom: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  @media (max-width: 1400px) {
    padding-bottom: 70px;
  }
  @media (max-width: 1000px) {
    padding-bottom: 60px;
  }
  @media (max-width: 800px) {
    padding-bottom: 70px;
  }
  // @media (max-width: px) {}
}
.app-screen-content-inn-center {
  margin: auto;
  width: 100%;
}

.svg {
  display: block;
  fill: currentColor;
  width: 1em;
  height: 1em;
}

.logo {
  position: absolute;
  top: 0;
  left: 0;
  width: 120px;
  height: 120px;
  z-index: 10;
  // background: red;
  background: #fff;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 44px;
  color: #1e1e1e;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: transform 0.5s ease 0.5s, opacity 0.5s ease 0.5s, background 0.35s ease, color 0.5s ease;
  @media (max-width: 1400px) {
    width: 100px;
    height: 100px;
    font-size: 38px;
  }
  @media (max-width: 1000px) {
    width: 90px;
    height: 90px;
    font-size: 34px;
  }
  @media (max-width: 800px) {
    width: 80px;
    height: 80px;
    font-size: 32px;
  }
  @media (max-width: 480px) {
    width: 55px;
    height: 55px;
    font-size: 28px;
  }
  .app.menu-open &{
    color: #fff;
    background: transparent;
    pointer-events: none;
  }
  .app.loaded & {
    transform: translate(0, 0);
    opacity: 1;
  }
}
.menu-btn {
  position: absolute;
  top: 0;
  right: 0;
  width: 120px;
  height: 120px;
  z-index: 8;
  cursor: pointer;
  transform: translate(50%, -50%);
  opacity: 0;
  transition: transform 0.5s ease 0.5s, opacity 0.5s ease 0.5s;
  @media (max-width: 1400px) {
    width: 100px;
    height: 100px;
  }
  @media (max-width: 1000px) {
    width: 90px;
    height: 90px;
  }
  @media (max-width: 800px) {
    width: 80px;
    height: 80px;
    background: #181818;
    transition: background  0.5s ease, transform 0.5s ease 0.5s, opacity 0.5s ease 0.5s;
  }
  @media (max-width: 480px) {
    width: 55px;
    height: 55px;
  }
  .app.loaded & {
    transform: translate(0, 0);
    opacity: 1;
  }
  span {
    position: absolute;
    display: block;
    width: 30px;
    height: 2px;
    // background: #fff;
    top: 50%;
    left: 50%;
    margin-left: -15px;
    margin-top: -1px;
    transition: margin 0.5s ease, opacity 0.5s ease, transform 0.5s ease;
    &:before {
      position: absolute;
      display: block;
      content: '';
      top: 0;
      left: 0;
      bottom: 0;
      width: 100%;
      background: #fff;
      transition: width 0.5s ease;
    }
    &:nth-child(1) {
      margin-top: -11px;
    }
    &:nth-child(2) {}
    &:nth-child(3) {
      margin-top: 9px;
      &:before {
        width: 70%;
      }
    }
  }
  &:hover {
    span {
      &:nth-child(1) {
        &:before {
          width: 70%;
        }
      }
      &:nth-child(3) {
        &:before {
          width: 100%;
        }
      }
    }
  }
  &.active {
    @media (max-width: 800px) {
      background: transparent;
    }
    span {
      &:before {
        width: 100% !important;
      }
      &:nth-child(1) {
        margin-top: -1px;
        transform: rotate(45deg);
      }
      &:nth-child(3) {
        margin-top: -1px;
        transform: rotate(-45deg);
      }
      &:nth-child(2) {
        opacity: 0;
      }
    }
  }
}

.nav {
  position: absolute;
  width: 120px;
  top: 120px;
  bottom: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  z-index: 2;
  opacity: 0;
  right: -30px;
  transition: opacity 0.5s ease 0.5s, right 0.5s ease 0.5s;
  @media (max-width: 1400px) {
    width: 100px;
    top: 100px;
  }
  @media (max-width: 1000px) {
    width: 90px;
    top: 90px;
  }
  @media (max-width: 800px) {
    right: 0;
    left: 0;
    bottom: -20px;
    top: auto;
    transition: opacity 0.5s ease 0.5s, bottom 0.5s ease 0.5s;
    width: auto;
    height: 50px;
    background: #181818;
    // display: none;
  }
  @media (max-width: 480px) {
  }
  .app.loaded & {
    opacity: 1;
    right: 0;
    @media (max-width: 800px) {
      bottom: 0px;
    }
  }
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    @media (max-width: 800px) {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
    }
  }
  li {
    cursor: pointer;
    text-align: center;
    color: #fff;
    font-weight: 300;
    font-size: 24px;
    transition: color 0.5s ease;
    position: relative;
    width: 100%;
    @media (max-width: 1400px) {
      font-size: 22px;
    }
    @media (max-width: 1000px) {
      font-size: 20px;
    }
    @media (max-width: 800px) {
      font-size: 0;
      width: 35px;
      margin-left: 7px;
      margin-right: 7px;
      height: 30px;
      // background: #fff;
      // opacity: 0.2;
      // transition: opacity 0.5s ease, background 0.5s ease;
    }
    @media (max-width: 480px) {
      width: 30px;
    }
    &:before {
      position: absolute;
      display: block;
      content: attr(data-text);
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      text-align: center;
      font-weight: 700;
      opacity: 0;
      transition: opacity 0.5s ease;
      color: #e09e36;
      @media (max-width: 800px) {
        display: none;
      }
    }
    &:after {
      display: block;
      content: '';
      position: absolute;
      top: 50%;
      right: 100%;
      width: 35px;
      height: 1px;
      background: #fff;
      opacity: 0.2;
      transition: background 0.5s ease, opacity 0.5s ease;
      @media (max-width: 1400px) {
        width: 30px;
      }
      @media (max-width: 1000px) {
        width: 25px;
      }
      @media (max-width: 800px) {
        // display: none;
        width: auto;
        left: 0;
        right: 0;
        height: 2px;
        margin-top: -1px;
      }
    }
    &.active {
      color: transparent;
      // @media (max-width: 800px) {
      //   opacity: 1;
      //   background: #dda32f;
      // }
      &:before {
        opacity: 1;
      }
      &:after {
        opacity: 1;
        background: #e09e36;
      }
    }
    & + li {
      margin-top: 30px;
      @media (max-width: 800px) {
        margin-top: 0;
        margin-left: 15px;
      }
    }
  }
}

.btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 80px;
  overflow: hidden;
  position: relative;
  background: #fff;
  font-size: 14px;
  font-weight: 900;
  text-transform: uppercase;
  color: #1e1e1e !important;
  text-decoration: none !important;
  line-height: 80px;
  @media (max-width: 1400px) {
    height: 74px;
    line-height: 74px;
  }
  @media (max-width: 1000px) {
    height: 66px;
    line-height: 66px;
  }
  @media (max-width: 800px) {
    height: 60px;
    line-height: 60px;
    font-size: 13px;
  }
  @media (max-width: 480px) {
    height: 54px;
    line-height: 54px;
  }
  &:before {
    display: block;
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: -50px;
    right: -50px;
    z-index: 1;
    background: #e09e36;
    transform: skewX(-50deg) translateX(-100%);
    transition: transform 0.5s ease;
  }
  &:after {
    display: block;
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    // line-height: 80px;
    color: #fff;
    z-index: 2;
    font-weight: 700;
    transform: translateX(-100%);
    transition: transform 0.5s ease;
  }
  &:hover {
    &:before {
      transform: skewX(-20deg) translateX(0%);
    }
    &:after {
      transform: translateX(0%);
    }
  }
}

.menu {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 7;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s ease, visibility 0.5s ease;
  overflow: hidden;
  &.active {
    visibility: visible;
    opacity: 1;
    transition: none;
  }
}
.menu-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  .menu.active & {
    &:before {
      transition: right 0.7s ease, transform 0.7s ease 0.15s;
      transform: skewX(-25deg);
      right: 49%;
    }
    &:after {
      left: 49%;
      transition: left 0.7s ease, transform 0.7s ease 0.15s;
      transform: skewX(-25deg);
    }
  }
  &:before {
    display: block;
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: -50%;
    right: 150%;
    transform: skewX(0deg);
    background: #1e1e1e;
    transition: right 0s ease 0.5s, transform 0s ease 05s;
    z-index: 1;
    @media (max-width: 480px) {
      left: -80%;
    }
  }
  &:after {
    display: block;
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: -50%;
    left: 150%;
    transform: skewX(0deg);
    background: #1e1e1e;
    transition: left 0s ease 0.5s, transform 0s ease 05s;
    z-index: 1;
    @media (max-width: 480px) {
      right: -80%;
    }
  }
  .menu.active & {
    span {
      transition: top 1s ease 1s, bottom 1s ease 1s;
      &:nth-child(1) {
        top: 0%;
      }
      &:nth-child(2) {
        bottom: 0%;
      }
      &:nth-child(3) {
        bottom: 0%;
      }
      &:nth-child(4) {
        top: 0%;
      }
    }
  }
  span {
    position: absolute;
    width: 1px;
    background: #171717;
    left: 50%;
    z-index: 2;
    transition: top 0s ease 0.5s, bottom 0s ease 0.5s;
    @media (max-width: 960px) {
      display: none;
    }
    &:nth-child(1) {
      bottom: 0;
      top: 100%;
      margin-left: -160px;
    }
    &:nth-child(2) {
      bottom: 100%;
      top: 0%;
      margin-left: 160px;
    }
    &:nth-child(3) {
      bottom: 100%;
      top: 0%;
      margin-left: -480px;
    }
    &:nth-child(4) {
      bottom: 0;
      top: 100%;
      margin-left: 480px;
    }
  }
  .menu.active & {
    b {
      transition: opacity 1s ease 1.5s;
      opacity: 1;
    }
  }
  b {
    position: absolute;
    top: 50%;
    margin-top: -375px;
    left: 50%;
    margin-left: -696px;
    z-index: 3;
    font-size: 233px;
    color: #171717;
    writing-mode: tb-rl;
    transform: rotate(180deg);
    transition: opacity 0s ease 0.5s;
    opacity: 0;
    @media (max-width: 1340px) {
      left: 40px;
      margin-left: 0;
      line-height: 170px;
    }
    @media (max-width: 960px) {
      color: rgba(23, 23, 23, 0.54);
    }
    @media (max-height: 750px) {
      font-size: 150px;
      margin-top: -240px;
      line-height: 110px;
      left: 30px;
    }
  }

}
.menu-scroll {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.menu-cnt {
  width: 1600px;
  margin: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  padding-top: 100px;
  padding-bottom: 100px;
  @media (max-width: 1600px) {
    width: 960px;
  }
  @media (max-width: 100px) {
    padding-top: 90px;
    padding-bottom: 90px;
  }
  @media (max-width: 960px) {
    width: 100%;
    padding-left: 100px;
    padding-right: 100px;
    justify-content: center;
  }
  @media (max-width: 800px) {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  @media (max-width: 480px) {
    padding-left: 70px;
    padding-right: 70px;
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
.menu-cnt-right {
  width: 640px;
  padding-right: 120px;
  @media (max-width: 960px) {
    width: 480px;
    padding-right: 0;
  }
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  li + li {
    margin-top: 80px;
    @media (max-height: 800px) {
      margin-top: 60px;
    }
    @media (max-width: 1000px) {
      margin-top: 60px;
    }
    @media (max-width: 800px) {
      margin-top: 40px;
    }
    // @media (max-width: 600px) {
    //   margin-top: 30px;
    // }
    // @media (max-width: 480px) {
    //   margin-top: 25px;
    // }
  }
  li {
    position: relative;
    left: -30px;
    opacity: 0;
    transition: left 0s ease 0.5s, opacity 0s ease 0.5s;
  }
  .menu.active & {
    li {
      left: 0;
      opacity: 1;
    }
    li:nth-child(1) {
      transition: left 1s ease 1.5s, opacity 1s ease 1.5s;
    }
    li:nth-child(2) {
      transition: left 1s ease 1.65s, opacity 1s ease 1.65s;
    }
    li:nth-child(3) {
      transition: left 1s ease 1.8s, opacity 1s ease 1.8s;
    }
    li:nth-child(4) {
      transition: left 1s ease 1.95s, opacity 1s ease 1.95s;
    }
    li:nth-child(5) {
      transition: left 1s ease 2.1s, opacity 1s ease 2.1s;
    }
    li:nth-child(6) {
      transition: left 1s ease 2.25s, opacity 1s ease 2.25s;
    }
  }
}
.menu-it {
  text-decoration: none !important;
  display: block;
  @media (max-width: 800px) {
    text-align: center;
  }
}
.menu-it-title {
  display: inline-block;
  vertical-align: top;
  position: relative;
  font-size: 30px;
  line-height: 1;
  color: #fff;
  font-weight: 700;
  padding-left: 5px;
  padding-right: 5px;
  margin-left: -5px;
  white-space: nowrap;
  @media (max-width: 1000px) {
    font-size: 26px;
  }
  @media (max-width: 800px) {
    font-size: 24px;
    font-weight: 500;
  }
  @media (max-width: 480px) {
    font-size: 22px;
  }
  &:before {
    display: block;
    content: '';
    position: absolute;
    top: -3px;
    bottom: -3px;
    background: #e09e36;
    left: 0%;
    right: 100%;
    transition: left 0.35s ease, right 0.35s ease 0.35s;
    transition-timing-function: cubic-bezier(0.7,0,0.3,1);
  }
  &:after {
    display: block;
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    height: 4px;
    margin-top: -1px;
    background: #e09e36;
    opacity: 0;
    transition: opacity 0s ease 0.35s;
    // @media (max-width: 480px) {
    //   display: none;
    // }
  }
  a:hover & {
    &:after {
      opacity: 1;
    }
    &:before {
      transition: right 0.35s ease, left 0.35s ease 0.35s;
      transition-timing-function: cubic-bezier(0.7,0,0.3,1);
      left: 100%;
      right: 0%;
    }
  }
}
.menu-it-text {
  font-size: 16px;
  line-height: 22px;
  margin-top: 10px;
  color: #828282;
  transition: color 1s;
  transition-timing-function: cubic-bezier(0.7,0,0.3,1);
  @media (max-width: 800px) {
    font-size: 14px;
    line-height: 20px;
    margin-top: 6px;
    display: none;
  }
  @media (max-width: 480px) {
    font-size: 13px;
    margin-top: 4px;
    line-height: 18px;
  }
  a:hover & {
    color: #fff;
  }
}

.demo {
  color: #fff;
  font-size: 16px;
  text-align: justify;
  @media (max-width: 800px) {
    font-size: 15px;
  }
  @media (max-width: 480px) {
    font-size: 14px;
  }
}

