.app-screen-tab_nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 120px;
  padding-left: 120px;
  padding-right: 120px;
  background: #181818;
  z-index: 2;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s ease, visibility 0.5s ease;
  .app-screen.active & {
    opacity: 1;
    visibility: visible;
  }
  @media (max-width: 1400px) {
    height: 100px;
    padding-left: 100px;
    padding-right: 100px;
  }
  @media (max-width: 1000px) {
    height: 90px;
    padding-left: 90px;
    padding-right: 90px;
  }
  @media (max-width: 800px) {
    height: 80px;
    padding-left: 80px;
    padding-right: 80px;
  }
  @media (max-width: 480px) {
    height: 55px;
    padding-left: 55px;
    padding-right: 55px;
  }
}
.app-screen-tab_nav-item {
  width: 100%;
  background: transparent;
  transition: background 0.5s ease, color 0.5s ease;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;
  color: #e9af4e;
  padding: 20px 15px;
  font-size: 18px;
  line-height: 1.2;
  cursor: pointer;
  text-align: center;
  @media (max-width: 1200px) {
    font-size: 16px;
  }
  @media (max-width: 1000px) {
    font-size: 14px;
  }
  &:before {
    display: block;
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -7px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 6px 7px 0 7px;
    border-color: #0e0e0e transparent transparent transparent;
    opacity: 0;
    transition: opacity 0.5s ease;
  }
  &.active {
    background: #0e0e0e;
    color: #fff;
    &:before {
      opacity: 1;
    }
  }
  &:hover {
    background: #0e0e0e;
  }
}
.app-screen-tab_nav-item-icon {
  font-size: 24px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  @media (max-width: 1200px) {
    font-size: 20px;
  }
  @media (max-width: 800px) {
    font-size: 26px;
  }
  @media (max-width: 480px) {
    font-size: 20px;
  }
}
.app-screen-tab_nav-item-text {
  margin-top: 15px;
  @media (max-width: 1200px) {
    margin-top: 10px;
  }
  @media (max-width: 800px) {
    display: none;
  }
}
.app-screen-tabs {
  position: relative;
  overflow: hidden;
}
.app-screen-tabs-tab {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  z-index: 1;
  &.active {
    opacity: 1;
    visibility: visible;
    z-index: 2;
    position: relative;
    transition: opacity 0.5s ease, visibility 0.5s ease;
  }
}
.app-screen-footer {
  width: 100%;
  background: #171717;
  text-align: center;
  padding: 20px 0px;
  font-size: 14px;
  color: #676767;
  line-height: 1.3;
  position: relative;
  @media (max-width: 800px) {
    display: none;
  }
  &:before {
    display: block;
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 100%;
    width: 3000px;
    background: #171717;
  }
  &:after {
    display: block;
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: 100%;
    width: 3000px;
    background: #171717;
  }
  a {
    text-decoration: underline !important;
    color: #676767 !important;
    transition: color 0.5s ease;
    &:hover {
      color: #e9af4e !important;
    }
  }
}

