.home_viewport {
  background-color: #1e1e1e;
  background-image: url(../img/template/home1-bg.jpg);
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
}
.homescreen_1 {
}

.homescreen_1-text1 {
  font-size: 170px;
  font-weight: 900;
  color: #d89a3d;
  line-height: 1;
  max-width: 680px;
  @media (max-width: 1400px) {
    font-size: 140px;
  }
  @media (max-width: 1200px) {
    font-size: 120px;
  }
  @media (max-width: 1000px) {
    font-size: 100px;
  }
  @media (max-width: 800px) {
    font-size: 88px;
  }
  @media (max-width: 480px) {
    font-size: 66px;
  }
}
.homescreen_1-text1-line {
  display: inline-block;
  vertical-align: top;
  position: relative;
  &:before {
    display: block;
    content: '';
    position: absolute;
    top: 10px;
    bottom: 10px;
    background: #d89a3d;
  }
  span {
    opacity: 0;
  }
  &:first-child {
    span {
      transition: opacity 0s ease 1.6s;
    }
    &:before {
      left: 0%;
      right: 100%;
      transition: right 0.6s ease 1s, left 0.6s ease 1.6s;
    }
  }
  &:last-child {
    span {
      transition: opacity 0s ease 2.3s;
    }
    &:before {
      left: 100%;
      right: 0%;
      transition: left 0.6s ease 1.7s, right 0.6s ease 2.3s;
    }
  }
  .app-screen.animated & {
    span {
      opacity: 1;
    }
    &:first-child {
      &:before {
        right: 0%;
        left: 100%;
      }
    }
    &:last-child {
      &:before {
        left: 0%;
        right: 100%;
      }
    }
  }
  html.backgroundcliptext & {
    span {
      background-image: url(../img/template/golden_pattern.jpg);
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
      -webkit-text-fill-color: transparent;
      background-clip: text;
    }
  }
}
.homescreen_1-text2 {
  margin-top: 50px;
  padding-left: 120px;
  position: relative;
  font-size: 24px;
  line-height: 36/24;
  color: #fff;
  max-width: 550px;
  opacity: 0;
  left: -30px;
  transition: opacity 0.65s ease 2.5s, left 0.65s ease 2.5s;
  @media (max-width: 1200px) {
    font-size: 22px;
    margin-top: 40px;
  }
  @media (max-width: 1000px) {
    font-size: 20px;
    margin-top: 30px;
    padding-left: 100px;
  }
  @media (max-width: 800px) {
    font-size: 18px;
    padding-left: 65px;
  }
  @media (max-width: 480px) {
    font-size: 16px;
    padding-left: 55px;
    margin-top: 22px;
  }
  .app-screen.animated & {
    opacity: 1;
    left: 0;
  }

  &:before {
    position: absolute;
    display: block;
    content: '';
    width: 80px;
    height: 1px;
    background: #fff;
    left: 0;
    top: 17px;
    @media (max-width: 1200px) {
      top: 16px;
    }
    @media (max-width: 1000px) {
      top: 15px;
      width: 60px;
    }
    @media (max-width: 800px) {
      width: 35px;
      top: 14px;
    }
  }
}
.homescreen_2-text {
  font-size: 24px;
  color: #fff;
  line-height: 40/24;
  max-width: 870px;
  position: relative;
  opacity: 0;
  transition: opacity 1.5s ease 0.8s;
  @media (max-width: 1400px) {
    font-size: 20px;
  }
  @media (max-width: 1200px) {
    font-size: 18px;
    max-width: 720px;
  }
  @media (max-width: 1000px) {
    font-size: 17px;
    max-width: 620px;
  }
  @media (max-width: 800px) {
    font-size: 15px;
  }
  @media (max-width: 480px) {
    font-size: 14px;
  }
  p + p {
    margin-top: 28px;
    @media (max-width: 1400px) {
      margin-top: 25px;
    }
    @media (max-width: 1200px) {
      margin-top: 22px;
    }
    @media (max-width: 1000px) {
      margin-top: 20px;
    }
    @media (max-width: 480px) {
      margin-top: 12px;
    }
  }
  .app-screen.animated & {
    opacity: 1;
  }
}
.homescreen_2 {
  .homescreen_2-btn {
    top: -30px;
    opacity: 0;
    transition: top 0.5s ease 1.5s, opacity 0.5s ease 1.5s;
  }
  &.app-screen.animated .homescreen_2-btn {
    opacity: 1;
    top: 0;
  }
}
.homescreen_2-btn {
  position: relative;
  width: 300px;
  margin-top: 60px;
  @media (max-width: 1400px) {
    margin-top: 50px;
  }
  @media (max-width: 1200px) {
    margin-top: 45px;
  }
  @media (max-width: 1000px) {
    margin-top: 40px;
    width: 275px;
  }
  @media (max-width: 800px) {
    width: 240px;
  }
  @media (max-width: 480px) {
    width: 220px;
    margin-top: 30px;
  }
}