.projectsscreen_1 {
  .app-screen-footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding-left: 20px;
    padding-right: 20px;
    z-index: 2;
    transition: opacity 0.5s ease 0.5s, bottom 0.5s ease 0.5s;
    opacity: 0;
    bottom: -20px;
  }
  .app.loaded & {
    .app-screen-footer {
      opacity: 1;
      bottom: 0;
    }
  }
}
.projectsscreen_1-slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  .slick-list,
  .slick-track {
    height: 100%;
  }
  .app.loaded & {
    .slick-dots {
      opacity: 1;
      bottom: 80px;
      @media (max-width: 800px) {
        bottom: 0;
      }
    }
  }
  .slick-dots {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 80px;
    z-index: 10;
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    transition: opacity 0.5s ease 0.5s, bottom 0.5s ease 0.5s;
    opacity: 0;
    bottom: 60px;
    @media (max-width: 800px) {
      height: 50px;
      bottom: -20px;
      background: #181818;
    }
    li {
      width: 35px;
      height: 35px;
      @media (max-width: 800px) {
        width: 30px;
        height: 30px;
      }
      position: relative;
      cursor: pointer;
      &:before {
        display: block;
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        height: 1px;
        background: #fff;
        opacity: 0.35;
        @media (max-width: 800px) {
          height: 2px;
          margin-top: -1px;
        }
      }
      &.slick-active {
        &:before {
          opacity: 1;
          background: #dba132;
        }
      }
      & + li {
        margin-left: 20px;
        @media (max-width: 800px) {
          margin-left: 15px;
        }
      }
    }
    button {
      display: none !important;
    }
  }
}
.projectsscreen_1-slide {
  background: #000;
  outline: 0 !important;
  overflow: hidden;
  position: relative;
  opacity: 0;
  .app.loaded & {
    opacity: 1;
  }
  &:nth-child(1) {
    transition: opacity 0.7s ease 0.7s;
  }
  &:nth-child(2) {
    transition: opacity 0.7s ease 0.9s;
  }
  &:nth-child(3) {
    transition: opacity 0.7s ease 1.1s;
  }
  &:nth-child(4) {
    transition: opacity 0.7s ease 1.3s;
  }
}
.projectsscreen_1-slide-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  transform: scale(1.1);
  transition: transform 0.5s ease, filter 0.5s ease, opacity 0.5s ease;
  z-index: 1;
  filter: grayscale(1);
  opacity: 0.7;
  @media (max-width: 800px) {
    transform: scale(1);
    filter: grayscale(0);
    opacity: 1;  
  }
  &:hover {
    transform: scale(1);
    filter: grayscale(0);
    opacity: 1;
  }
}
.projectsscreen_1-slide-title {
  position: absolute;
  z-index: 2;
  left: 0;
  right: 0;
  padding-left: 15px;
  padding-right: 15px;  
  bottom: 170px;
  text-align: center;
  font-size: 30px;
  line-height: 1.2;
  color: #fff;
  font-weight: 700;
  transition: bottom 0.5s ease;
  text-transform: uppercase;
  @media (max-width: 800px) {
    bottom: 180px;
    font-size: 26px;
  }
  .projectsscreen_1-slide:hover & {
    bottom: 205px;
    @media (max-width: 800px) {
      bottom: 180px;
    }
  }
}
.projectsscreen_1-slide-text {
  position: absolute;
  z-index: 2;
  left: 0;
  right: 0;
  padding-left: 15px;
  padding-right: 15px;
  text-align: center;
  font-size: 18px;
  line-height: 26/18;
  color: #fff;
  height: 110px;
  bottom: 90px;
  opacity: 0;
  transition: opacity 0.5s ease;
  @media (max-width: 800px) {
    opacity: 1;
    font-size: 16px;
    bottom: 60px;
  }
  .projectsscreen_1-slide:hover & {
    opacity: 1;
  }
  p {
    margin-left: auto;
    margin-right: auto;
    margin-top: 0;
    margin-bottom: 0;
    display: block;
    max-width: 255px;
  }
}