.resumescreen_1 {
  background-color: #1e1e1e;
  background-image: url(../img/template/home1-bg.jpg);
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
}
.resumescreen_1-text {
  position: relative;
  font-size: 18px;
  font-weight: 400;
  line-height: 30/18;
  color: #fff;
  max-width: 1480px;
  margin-top: 50px;
  @media (max-width: 1400px) {}
  margin-top: 45px;
  @media (max-width: 1000px) {
    font-size: 17px;
    margin-top: 40px;
  }
  @media (max-width: 800px) {
    font-size: 15px;
    margin-top: 35px;
  }
  @media (max-width: 480px) {
    font-size: 14px;
    margin-top: 30px;
  }
  p + p {
    margin-top: 28px;
    @media (max-width: 1400px) {
      margin-top: 25px;
    }
    @media (max-width: 1200px) {
      margin-top: 22px;
    }
    @media (max-width: 1000px) {
      margin-top: 20px;
    }
    @media (max-width: 480px) {
      margin-top: 12px;
    }
  }
}
.resumescreen_1 {
  .app-screen-title {
    top: -30px;
    opacity: 0;
    transition: top 0.75s ease 1s, opacity 0.75s ease 1s;
  }
  .resumescreen_1-text {
    top: -30px;
    opacity: 0;
    transition: top 0.75s ease 1.2s, opacity 0.75s ease 1.2s;
  }
  .homescreen_2-btn {
    top: 30px;
    opacity: 0;
    transition: top 0.75s ease 1.5s, opacity 0.75s ease 1.5s;
  }
  &.app-screen.animated {
    .app-screen-title {
      top: 0;
      opacity: 1;
    }
    .resumescreen_1-text {
      top: 0;
      opacity: 1;
    }
    .homescreen_2-btn {
      top: 0;
      opacity: 1;
    }
  }
}
.resumescreen_2 {
  background: #1e1e1e;
  .app-screen-title {
    top: -30px;
    opacity: 0;
    transition: top 0.7s ease 1s, opacity 0.7s ease 1s;
  }
  .resumescreen_2-services {
    top: 30px;
    position: relative;
    opacity: 0;
    transition: top 0.7s ease 1.3s, opacity 0.7s ease 1.3s;
  }
  .resumescreen_2-education-img {
    left: 30px;
    opacity: 0;
    transition: left 0.7s ease 1.3s, opacity 0.7s ease 1.3s;
  }
  .resumescreen_2-education-text {
    position: relative;
    left: -30px;
    opacity: 0;
    transition: left 0.7s ease 1.5s, opacity 0.7s ease 1.5s;
  }
  .resumescreen_2-skills-block,
  .resumescreen_2-qualities-block {
    opacity: 0;
    position: relative;
    left: -30px;
    // transition: opacity time;
    &:nth-child(1) {
      transition: opacity 0.7s ease 1s, left 0.7s ease 1s;
      .resumescreen_2-skills-block-line-inn {
        transition: width 0.5s ease 0.5s;
      }
    }
    &:nth-child(2) {
      transition: opacity 0.7s ease 1.2s, left 0.7s ease 1.2s;
      .resumescreen_2-skills-block-line-inn {
        transition: width 0.5s ease 0.7s;
      }
    }
    &:nth-child(3) {
      transition: opacity 0.7s ease 1.4s, left 0.7s ease 1.4s;
      .resumescreen_2-skills-block-line-inn {
        transition: width 0.5s ease 0.9s;
      }
    }
    &:nth-child(4) {
      transition: opacity 0.7s ease 1.6s, left 0.7s ease 1.6s;
      .resumescreen_2-skills-block-line-inn {
        transition: width 0.5s ease 1.1s;
      }
    }
    &:nth-child(5) {
      transition: opacity 0.7s ease 1.8s, left 0.7s ease 1.8s;
      .resumescreen_2-skills-block-line-inn {
        transition: width 0.5s ease 1.2s;
      }
    }
    &:nth-child(6) {
      transition: opacity 0.7s ease 2s, left 0.7s ease 2s;
      .resumescreen_2-skills-block-line-inn {
        transition: width 0.5s ease 1.4s;
      }
    }
    &:nth-child(7) {
      transition: opacity 0.7s ease 2.2s, left 0.7s ease 2.2s;
      .resumescreen_2-skills-block-line-inn {
        transition: width 0.5s ease 1.6s;
      }
    }
    &:nth-child(8) {
      transition: opacity 0.7s ease 2.4s, left 0.7s ease 2.4s;
      .resumescreen_2-skills-block-line-inn {
        transition: width 0.5s ease 1.8s;
      }
    }
    &:nth-child(9) {
      transition: opacity 0.7s ease 2.6s, left 0.7s ease 2.6s;
      .resumescreen_2-skills-block-line-inn {
        transition: width 0.5s ease 2s;
      }
    }
  }
  &.app-screen.animated {
    .app-screen-tabs-tab.animated {
      .app-screen-title,
      .resumescreen_2-services {
        top: 0;
        opacity: 1;
      }
      .resumescreen_2-skills-block,
      .resumescreen_2-qualities-block,
      .resumescreen_2-education-img,
      .resumescreen_2-education-text {
        opacity: 1;
        left: 0;
      }
    }
  }
}
.resumescreen_2-skills {
  // margin-top: 100px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;
  @media (max-width: 800px) {
    margin-left: -20px;
    margin-right: -20px;
  }
}
.resumescreen_2-skills-block {
  width: 33.333%;
  padding-right: 60px;
  margin-top: 100px;
  @media (max-width: 1400px) {
    margin-top: 80px;
    padding-right: 50px;
  }
  @media (max-width: 1200px) {
    margin-top: 70px;
    padding-right: 45px;
  }
  @media (max-width: 1000px) {
    margin-top: 60px;
    padding-right: 40px;
    width: 50%;
  }
  @media (max-width: 800px) {
    margin-top: 50px;
    padding-right: 20px;
    padding-left: 20px;
  }
  @media (max-width: 480px) {
    margin-top: 40px;
    width: 100%;
  }
}
.resumescreen_2-skills-block-line {
  max-width: 340px;
  height: 10px;
  border-radius: 5px;
  background: #2b2b2b;
  position: relative;
  overflow: hidden;
  @media (max-width: 480px) {
    height: 7px;
  }
}
.resumescreen_2-skills-block-line-inn {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  bottom: 0;
  width: 0%;
  background-image: url(../img/template/golden_pattern.jpg);
  background-position: top left;
  background-repeat: no-repeat;
  transition: width 0.5s ease;
  border-radius: 5px;
}
.resumescreen_2-skills-block-text {
  font-size: 24px;
  color: #fff;
  line-height: 1.2;
  margin-top: 40px;
  @media (max-width: 1400px) {
    font-size: 22px;
    margin-top: 32px;
  }
  @media (max-width: 1200px) {
    font-size: 20px;
    margin-top: 30px;
  }
  @media (max-width: 800px) {
    font-size: 18px;
    margin-top: 20px;
  }
  @media (max-width: 480px) {
    font-size: 16px;
    margin-top: 15px;
  }
}
.resumescreen_2-qualities {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  @media (max-width: 800px) {
    margin-left: -20px;
    margin-right: -20px;
  }
}
.resumescreen_2-qualities-block {
  width: 33.333%;
  padding-right: 60px;
  margin-top: 100px;
  font-size: 24px;
  color: #fff;
  line-height: 1.2;
  position: relative;
  padding-left: 50px;
  @media (max-width: 1400px) {
    margin-top: 80px;
    padding-right: 50px;
    font-size: 22px;
    padding-left: 45px;
  }
  @media (max-width: 1200px) {
    margin-top: 70px;
    padding-right: 45px;
    font-size: 18px;
    padding-left: 40px;
  }
  @media (max-width: 1000px) {
    margin-top: 60px;
    padding-right: 40px;
    width: 50%;
    // font-size: 18px;
  }
  @media (max-width: 800px) {
    margin-top: 50px;
    padding-right: 20px;
    padding-left: 50px;
    font-size: 16px;
  }
  @media (max-width: 480px) {
    margin-top: 40px;
    width: 100%;
    font-size: 16px;
  }
  &:before {
    display: block;
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    width: 16px;
    height: 16px;
    margin-top: -8px;
    background-image: url(../img/template/golden_pattern.jpg);
    z-index: 1;
    @media (max-width: 800px) {
      left: 20px;
    }
  }
  &:after {
    display: block;
    content: '';
    position: absolute;
    top: 50%;
    left: 4px;
    width: 8px;
    height: 8px;
    margin-top: -4px;
    background: #1e1e1e;
    z-index: 2;
    @media (max-width: 800px) {
      left: 24px;
    }
  }
}
.resumescreen_2-services {
  margin-top: 60px;
  max-width: 1550px;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  flex-wrap: wrap;
  position: relative;
  @media (max-width: 1000px) {
    margin-top: 50px;
  }
  @media (max-width: 800px) {
    margin-top: 40px;
  }
  @media (max-width: 480px) {
    margin-top: 30px;
  }
  &:before {
    display: block;
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 1px;
    z-index: 2;
    background: #1e1e1e;
  }
  &:after {
    display: block;
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    z-index: 2;
    background: #1e1e1e;
  }
}
.resumescreen_2-services-block {
  width: 100% / 8;
  position: relative;
  // background: red;
  border-bottom: 1px solid #292929;
  border-right: 1px solid #292929;
  @media (max-width: 1200px) {
    width: 100% / 7;
  }
  @media (max-width: 1000px) {
    width: 100% / 6;
  }
  @media (max-width: 650px) {
    width: 100% / 5;
  }
  @media (max-width: 480px) {
    width: 100% / 4;
  }
  @media (max-width: 400px) {
    width: 100% / 3;
  }
  img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    max-width: 85%;
    max-height: 85%;
    width: auto;
    height: auto;
    margin: auto;
    opacity: 0.7;
    filter: grayscale(1);
    transition: opacity 0.5s ease, filter 0.5s ease;
  }
  &:hover {
    img {
      filter: grayscale(0);
      opacity: 1;
    }
  }
  &:before {
    display: block;
    content: '';
    padding-bottom: 90%;
  }
}
.resumescreen_2-education {
  min-height: 500px;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  max-width: 1550px;
  padding-left: 90px;
  margin-top: 70px;
  @media (max-width: 1200px) {
    margin-top: 60px;
    min-height: 400px;
    padding-left: 70px;
  }
  @media (max-width: 1000px) {
    margin-top: 50px;
    min-height: 350px;
    padding-left: 60px;
  }
  @media (max-width: 800px) {
    margin-top: 40px;
    padding-left: 50px;
  }
  @media (max-width: 480px) {
    margin-top: 30px;
    padding-left: 10px;
    padding-right: 10px;
    min-height: 300px;
  }
}
.resumescreen_2-education-img {
  position: absolute;
  top: 50%;
  left: 0;
  font-size: 500px;
  width: 1em;
  height: 1em;
  margin-top: -0.5em;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  @media (max-width: 1200px) {
    font-size: 400px;
  }
  @media (max-width: 1000px) {
    font-size: 350px;
  }
  @media (max-width: 480px) {
    width: 100%;
    top: 0;
    margin-top: 0;
    height: 300px;
  }
}
.resumescreen_2-education-text {
  color: #fff;
  font-size: 24px;
  line-height: 36/24;
  position: relative;
  z-index: 2;
  @media (max-width: 1200px) {
    font-size: 20px;
  }
  @media (max-width: 800px) {
    font-size: 18px;
  } 
  @media (max-width: 480px) {
    font-size: 16px;
  }
  p {
    margin-top: 0;
    margin-bottom: 0;
  } 
  h3 {
    font-size: 30px;
    font-weight: 700;
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 0;
    padding-bottom: 0;
    margin-bottom: 30px;
    line-height: 1.4;
    @media (max-width: 1200px) {
      font-size: 26px;
      margin-bottom: 25px;
    }
    @media (max-width: 800px) {
      font-size: 22px;
      margin-bottom: 16px;
    }
    @media (max-width: 480px) {
      font-size: 20px;
    }
  }
}
.resumescreen_3 {
  background: #1e1e1e;
  .app-screen-title {
    top: -30px;
    opacity: 0;
    transition: top 0.7s ease 1s, opacity 0.7s ease 1s;
  }
  .resumescreen_3-text {
    position: relative;
    top: -30px;
    opacity: 0;
    transition: top 0.7s ease 1.2s, opacity 0.7s ease 1.2s;
  }
  .resumescreen_3-slider_arrs {
    position: relative;
    top: 30px;
    opacity: 0;
    transition: top 0.7s ease 1.4s, opacity 0.7s ease 1.4s;
  }
  .resumescreen_3-slider-wrap {
    position: relative;
    top: 30px;
    opacity: 0;
    transition: top 0.7s ease 1.6s, opacity 0.7s ease 1.6s;
  }
  .app-screen-footer {
    transition: transform 0.7s ease 1s;
    transform: translateY(100%);
  }
  &.app-screen.animated {
    .app-screen-title,
    .resumescreen_3-text,
    .resumescreen_3-slider_arrs,
    .resumescreen_3-slider-wrap {
      top: 0;
      opacity: 1;
    }
    .app-screen-footer {
      transform: translateY(0%);
    }
  }

}
.resumescreen_3-text {
  margin-top: 45px;
  color: #fff;
  font-size: 24px;
  line-height: 36/24;
  max-width: 1500px;
  @media (max-width: 1400px) {
    margin-top: 40px;
    font-size: 22px;
  }
  @media (max-width: 1200px) {
    font-size: 20px;
  }
  @media (max-width: 1000px) {
    font-size: 18px;
    margin-top: 30px;
  }
  @media (max-width: 480px) {
    font-size: 16px;
    margin-top: 20px;
  }
  p {
    margin-top: 0;
    margin-bottom: 0;
    & + p {
      margin-top: 36px;
      @media (max-width: 1400px) {
        margin-top: 25px;
      }
      @media (max-width: 1000px) {
        margin-top: 20px;
      }
      @media (max-width: 480px) {
        margin-top: 15px;
      }
    }
  }
}
.resumescreen_3-slider_arrs {
  margin-top: 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  @media (max-width: 1400px) {
    margin-top: 50px;
  }
  @media (max-width: 1200px) {
    margin-top: 40px;
  }
  @media (max-width: 1000px) {
    margin-top: 30px;
  }
  @media (max-width: 800px) {
    margin-top: 20px;
  }
}
.resumescreen_3-slider_arrs-arr {
  font-size: 60px;
  width: 1em;
  height: 1em;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #fff;
  transition: color 0.5s ease, opacity 0.5s ease;
  @media (max-width: 800px) {
    font-size: 54px;
  }
  @media (max-width: 480px) {
    font-size: 46px;
  }
  &.__left {
    transform: rotate(180deg);
  }
  &:hover {
    color: #e09e36;
  }
  &.slick-disabled {
    opacity: 0.35;
    pointer-events: none;
  }
  & + .resumescreen_3-slider_arrs-arr {
    margin-left: 40px;
    @media (max-width: 800px) {
      margin-left: 30px;
    }
  }
}
.resumescreen_3-slider-wrap {
  margin-top: 40px;
  @media (max-width: 1400px) {
    margin-top: 30px;
  }
  @media (max-width: 1200px) {
    margin-top: 20px;
  }
  @media (max-width: 1000px) {
    margin-top: 10px;
  }
  @media (max-width: 800px) {
    margin-top: 22px;
  }
  @media (max-width: 480px) {
    margin-left: -15px;
    margin-right: -15px;
  }
  .slick-list {
    overflow: visible !important;
  }
}
.resumescreen_3-slider-slide {
  // width: 33.333%;
  padding-right: 40px;
  outline: 0 !important;
  pointer-events: none;
  @media (max-width: 480px) {
    padding-right: 0;
  }
  &.slick-active {
    pointer-events: all;
  }
}
.resumescreen_3-slider {
  position: relative;
  &:before {
    display: block;
    content: '';
    position: absolute;
    width: 3000px;
    top: 0;
    bottom: 0;
    left: 100%;
    z-index: 1;
    background: #1e1e1e;
    margin-left: 100px;
    box-shadow: 0 0 100px 80px #1e1e1e;
  }
  &:after {
    display: block;
    content: '';
    position: absolute;
    width: 3000px;
    top: 0;
    bottom: 0;
    right: 100%;
    z-index: 1;
    background: #1e1e1e;
    margin-right: 100px;
    box-shadow: 0 0 100px 80px #1e1e1e;
  }
}
.resumescreen_3-slider-block {
  max-width: 420px;
  padding: 40px;
  background: transparent;
  transition: background 0.5s ease, box-shadow 0.5s ease;
  @media (max-width: 1400px) {
    padding: 30px;
  }
  @media (max-width: 1000px) {
    padding: 22px;
  }
  @media (max-width: 800px) {
    padding: 0px 15px;
  }
  &:hover {
    box-shadow: 0px 15px 35px 0px rgba(0, 0, 0, 0.7);
    background: #fff;
    @media (max-width: 800px) {
      background: transparent;
      box-shadow: none;
    }
  }
}

.resumescreen_3-slider-block-year {
  font-size: 30px;
  line-height: 35/30;
  color: #fff;
  font-weight: 700;
  transition: color 0.5s ease;
  @media (max-width: 1400px) {
    font-size: 26px;
  }
  @media (max-width: 1000px) {
    font-size: 22px;
  }
  .resumescreen_3-slider-block:hover & {
    color: #1e1e1e;
    @media (max-width: 800px) {
      color: #fff;
    }
  }
}
.resumescreen_3-slider-block-name {
  font-size: 30px;
  line-height: 35/30;
  color: #fff;
  font-weight: 700;
  transition: color 0.5s ease;
  @media (max-width: 1400px) {
    font-size: 24px;
  }
  @media (max-width: 1000px) {
    font-size: 22px;
  }
  .resumescreen_3-slider-block:hover & {
    color: #1e1e1e;
    @media (max-width: 800px) {
      color: #fff;
    }
  }
}
.resumescreen_3-slider-block-position {
  font-size: 18px;
  line-height: 1.4;
  color: #fff;
  transition: color 0.5s ease;
  margin-top: 10px;
  @media (max-width: 1000px) {
    font-size: 16px;
    margin-top: 8px;
  }
  .resumescreen_3-slider-block:hover & {
    color: #1e1e1e;
    @media (max-width: 800px) {
      color: #fff;
    }
  }
}
.resumescreen_3-slider-block-link {
  margin-top: 30px;
  @media (max-width: 1400px) {
    margin-top: 25px;
  }
  @media (max-width: 1000px) {
    margin-top: 20px;
  }
  a {
    font-size: 18px;
    text-decoration: none !important;
    color: #f3bf69 !important;
    line-height: 1.4;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: color 0.5s ease;
    @media (max-width: 1400px) {
      font-size: 16px;
    }
    &:hover {
      color: #f3bf69 - #222 !important;
    }
  }
}